<template>
  <div class="device">
    <section class="device-list-panel">
      <DeviceListPanel />
    </section>
    <section :class="['monitor-panel',currentShow=='monitorpanel'?'pop-up':'']">
      <i
        :class="['font_family','icon',currentShow=='monitorpanel'?'icon-suoxiao':'icon-fangda-01']"
        @click="isShow('monitorpanel')"
      ></i>
      <MonitorPanel />
    </section>
    <section :class="['control-panel',currentShow=='controlpanel'?'pop-up':'']">
      <i
        :class="['font_family','icon',currentShow=='controlpanel'?'icon-suoxiao':'icon-fangda-01']"
        @click="isShow('controlpanel')"
      ></i>
      <ControlPanel ref="controlPanel" />
    </section>
  </div>
</template>

<script>
import DeviceListPanel from "@/components/device/DeviceListPanel.vue";
import MonitorPanel from "@/components/device/MonitorPanel.vue";
import ControlPanel from "@/components/device/ControlPanel.vue";

export default {
  name: "DevicePage",
  components: {
    DeviceListPanel,
    MonitorPanel,
    ControlPanel
  },
  data() {
    return {
      currentShow: null
    };
  },
  methods: {
    isShow(name) {
      this.currentShow = !this.currentShow ? name : null;
      this.$refs.controlPanel.tableOptions.height =
        name == "controlpanel" && this.currentShow ? "530px" : "140px";
    }
  }
};
</script>

<style lang="scss" scoped>
.device {
  position: relative;
  width: 1440px;
  height: 826px;
  padding: 32px 39px 35px 37px;
  background: #eaedf1;
  display: grid;
  grid-template-columns: 358px 967px;
  grid-template-rows: 380px 340px;
  grid-gap: 39px;

  .device-list-panel {
    grid-row: 1 / 3;
    background: #fff;
  }

  .icon {
    position: absolute;
    top: -8px;
    right: -8px;
    display: none;
  }

  .monitor-panel {
    position: relative;
    background: #fff;
    &:hover {
      .icon {
        display: block;
      }
    }
  }

  .control-panel {
    position: relative;
    background: #fff;
    // transition: 2s all linear;
    &:hover {
      .icon {
        display: block;
      }
    }
  }

  .pop-up {
    position: absolute;
    top: 32px;
    left: 37px;
    background: #fff;
    width: 1364px;
    height: 759px;
    z-index: 99;
  }
}
</style>