<template>
  <div class="device-list-panel-content">
    <div class="content-title">
      <i class="font_family iconfont icon-shebeiliebiao"></i>
      Device List Panel
    </div>
    <div class="content-main">
      <el-collapse class="content-main-collapse" v-model="activeNames" @change="handleChange">
        <el-collapse-item
          v-for="(items,indexs) in collapseList"
          :key="`devices${indexs}`"
          :name="`${indexs}`"
        >
          <template slot="title">
            {{items.title}}
            <span class="collapse-legend">{{`（共有${items.length}个设备）`}}</span>
          </template>
          <ul class="collapse-list">
            <li
              v-for="(item) in items.data"
              :class="['collapse-list-item',currentDevice == item.deviceId ? 'item-is-active' : '']"
              :key="`${item.deviceId}`"
              @click="selected(item.deviceId)"
            >
              {{item.deviceName}}
              <span
                :class="[item.status == 10 ? 'collapse-list-item-status' : '']"
              >{{`（${item.status}）`}}</span>
            </li>
          </ul>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
export default {
  name: "DeviceListPanel",
  data() {
    return {
      activeNames: ["0", "1", "2", "3"],
      collapseList: [],
      currentDevice: ""
    };
  },
  mounted() {
    this.getDevice();
  },
  methods: {
    handleChange(val) {
      // console.log(val);
    },
    async getDevice() {
      const url = "/device/listByFactoryId";
      const params = { factoryId: 2 };
      const responseData = await this.$http.get(url, {
        params
      });
      // console.log(responseData);
      if (responseData) {
        const data = responseData.data;
        const tempData = this.$lodash.groupBy(data, "deviceType");
        this.$lodash.forIn(tempData, (value, key) => {
          this.collapseList.push({
            title: this.$config.deviceType[key],
            length: value.length,
            data: value
          });
        });
        // console.log(this.collapseList);
      }
    },
    selected(deviceId) {
      this.currentDevice = deviceId;
    }
  }
};
</script>

<style lang="scss">
.device-list-panel-content {
  .content-main {
    height: 693px;
    overflow-y: auto;

    .content-main-collapse {
      border-top: none;
      border-bottom: none;

      .el-collapse-item {
        margin-bottom: 20px;

        .el-collapse-item__header {
          border-bottom: none;
          font-size: 12px;
          font-weight: 600;
          color: rgba(51, 51, 51, 1);
          padding-left: 64px;
          padding-right: 45px;

          .collapse-legend {
            font-weight: normal;
          }
        }
        .el-collapse-item__wrap {
          border-bottom: none;
          max-height: 160px;
          overflow-y: auto;

          .el-collapse-item__content {
            padding-bottom: 0px;

            .collapse-list {
              // width: 80%;

              .collapse-list-item {
                font-size: 12px;
                font-weight: 400;
                color: #333333;
                // margin-bottom: 13px;
                display: flex;
                justify-content: space-between;
                padding-left: 64px;
                padding-right: 78px;
                height: 30px;
                line-height: 30px;
                cursor: pointer;

                .collapse-list-item-status {
                  color: #e24e5f;
                }

                &.item-is-active {
                  border-left: 4px solid #0084e3;
                  padding-left: 60px;
                  background: #edf5fd;
                  color: #0084e3;
                }

                &:last-child {
                  margin-bottom: 0px;
                }
              }
            }
          }
        }

        &:last-child {
          margin-bottom: 0px;
        }
      }
    }
  }
}
</style>