<template>
  <div class="control-panel-content">
    <div class="content-title">
      <i class="font_family iconfont icon-kongzhi"></i>
      Control Panel
    </div>
    <Search />
    <div class="control-panel-table">
      <TableItem
        :tableOptions="tableOptions"
        :tableColumns="tableColumns"
        :tableDatas="tableDatas"
      />
    </div>
  </div>
</template>

<script>
import Search from "@/components/Search.vue";
import TableItem from "@/components/Table.vue";

export default {
  name: "ControlPanel",
  components: {
    Search,
    TableItem
  },
  data() {
    return {
      tableOptions: {
        height: "140px"
      },
      tableColumns: [
        {
          label: "Equipment",
          prop: "equipment"
        },
        {
          label: "Instruction ID",
          prop: "insid"
        },
        {
          label: "Requestor",
          prop: "requestor"
        },
        {
          label: "Instruction typeCat",
          prop: "intype"
        },
        {
          label: "Status",
          prop: "status"
        },
        {
          label: "Log",
          prop: "log"
        },
        {
          label: "Process",
          prop: "process",
          render: ({ process }) => {
            return `<span>${process}</span>`;
          }
        },
        {
          label: "Down time",
          prop: "dotime"
        }
      ],
      tableDatas: []
    };
  },
  mounted() {
    for (let i = 0; i < 20; i++) {
      this.tableDatas.push({
        equipment: `51${i}`,
        insid: `SWC0${i}`,
        requestor: `缝制`,
        intype: `10${i}`,
        status: `6${i}`,
        log: `缝制`,
        process: `工位1 --> 工位2`,
        dotime: `6${i}`
      });
    }
  },
  methods: {}
};
</script>

<style lang="scss">
.control-panel-table {
  padding: 0 33px;
}
</style>